#projects {
    background-color: #586F7C;
    text-align: center;
    color: #fffbfc;
}

.projects-wrap {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}

.project-img {
    max-width: 100%;
    /*width: 100%;*/
    /*height: 600px;*/
}

.img-wrap {
    width: 100%;
}

.project {
    width: 100%;
    text-align: center;
    font-size: 14px;
    margin: 1em auto;

}

.project-wrap {
    padding: 1em;
}

@media screen and (min-width: 768px) {
    .project {
        width: 50%;
    }
}

@media screen and (min-width: 1024px) {
  .project {
        width: 40%;
    }
}


